<template>
  <x-header>
    黄历
  </x-header>
  <inline-calendar
  class="inline-calendar-demo"
  :show.sync="show"
  :value.sync="value"
  start-date="2016-04-01"
  end-date="2017-06-18"
  :range="range"
  :show-last-month="showLastMonth"
  :show-next-month="showNextMonth"
  :highlight-weekend="highlightWeekend"
  :return-six-rows="return6Rows"
  :hide-header="hideHeader"
  :hide-week-list="hideWeekList"
  :replace-text-list="replaceTextList"
  :weeks-list="weeksList"
  :custom-slot-fn="buildSlotFn"
  :disable-past="disablePast">
  </inline-calendar>
  <article class="weui_article">
    <h1>大标题</h1>
    <section>
      <h2 class="title">章标题</h2>
      <section>
        <h3>1.1 节标题</h3>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
          tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
          quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
          consequat. Duis aute
        </p>
      </section>
    </section>
  </article>
</template>
<script>
  import XHeader from 'vux/components/x-header'
  import InlineCalendar from '../components/inline-calendar'

  export default {
    data () {
      return {
        leftOptions: {
          showBack: true,
          backText: 'Back',
          preventGoBack: true
        },
        weeksList: ['日', '一', '二', '三', '四', '五', '六'],
        value: '',
        result: {}
      }
    },
    methods: {
    },
    events: {
      'select-day' (date) {
        this.$http.get('http://japi.juhe.cn/calendar/day', { date: date, key: '50b1b6209ddd79e0562ce0f45d6c3c69' }).then(function (response) {
          this.$set('result', response.data)
        }, function (response) {
          this.showAlert(response.data.title, response.data.error)
        })
      }
    },
    components: {
      XHeader,
      InlineCalendar
    }
  }
</script>



